.Board {
    margin: 0 auto;
    display: -ms-flexbox;
    display: flex;
    width: 35vw;
    height: 35vw;
    max-width: 500px;
    max-height: 500px;
    -ms-flex-wrap: wrap;
        flex-wrap: wrap;
    -ms-flex-item-align: center;
        align-self: center;
    border: 2px solid black;
}

@media (orientation: portrait) {
    .Board {
        width: 40vh;
        height: 40vh;
    }
}

@media (max-width: 50rem) and (orientation: landscape) {
    .Board {
        width: 60vh;
        height: 60vh;
    }
}


