.Header {
    position: fixed;
    z-index: 1;
    display: -ms-flexbox;
    display: flex;
    -ms-flex-align: center;
        align-items: center;
    -ms-flex-pack: center;
        justify-content: center;
    width: 100%;
    height: 10%;
    background-color: firebrick;
    -webkit-box-shadow: 0px 3px 3px rgba(0,0,0,0.7);
            box-shadow: 0px 3px 3px rgba(0,0,0,0.7);
    font-weight: 700;
    font-size: 1.2rem;
    color: mintcream;
    text-shadow: 2px 2px black;
}

.heading {
    margin: 1rem 0;
}

/* @media (max-width: 40rem) {
    .Header {
        font-size: 0.9rem;
    }
} */

@media (max-width: 50rem) and (orientation: landscape) {
    .Header {
        font-size: 0.7rem;
    }
}



