.Modal {
    z-index: 3;
    position: absolute;
    display: -ms-flexbox;
    display: flex;
    -ms-flex-direction: column;
        flex-direction: column;
    -ms-flex-pack: space-evenly;
        justify-content: space-evenly;
    width: 60%;
    top: 20%;
    padding: 2rem 1rem;
    -webkit-transition: all 0.3s ease-out;
    -o-transition: all 0.3s ease-out;
    transition: all 0.3s ease-out;
    line-height: 150%;
    background-color: firebrick;
    color: mintcream;
    border: 2px solid darkred;
    text-align: center;
    font-size: 1rem;
}
.invisible {
    visibility: hidden;
    opacity: 0;
    -webkit-transform: translateX(-1000px);
        -ms-transform: translateX(-1000px);
            transform: translateX(-1000px);
}
.subheader {
    text-align: center;
    padding: 0;
    margin: 0;
}

.button {
    position: absolute;
    display: -ms-flexbox;
    display: flex;
    -ms-flex-align: center;
        align-items: center;
    -ms-flex-pack: center;
        justify-content: center;
    top: 0.3rem;
    right: 0.3rem;
    width: 1.2rem;
    height: 1.2rem;
    background-color: darkred;
    color: mintcream;
    border: red;
    font-weight: bold;
}
.button:active {
    background-color: orangered; 
}

@media (max-width: 40rem) {
    .Modal {
        width: 80%;
        top: 20%;
        height: 60%;
        font-size: 0.8rem;
        padding: 0.3rem 0;
    }
}

@media (max-height:26.5rem) {
    .Modal {
        width: 80%;
        top: 20%;
        height: 60%;
        font-size: 0.7rem;
        padding-top: 5px;
        padding-bottom: 5px;
    }
}


